<template><section><h2>Alert</h2>
<p>用于页面中展示重要的提示信息。</p>
<h3>基本用法</h3>
<div class="bs-example"><template>
  <bs-alert type="success">
    成功提示文案
  </bs-alert>
  <bs-alert type="info">
    消息提示文案
  </bs-alert>
  <bs-alert type="warning">
    警告提示文案
  </bs-alert>
  <bs-alert type="danger">
    错误提示文案
  </bs-alert>
</template>
</div>
                    <div class="highlight"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">bs-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"success"</span>&gt;</span>
    成功提示文案
  <span class="hljs-tag">&lt;/<span class="hljs-name">bs-alert</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">bs-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span>&gt;</span>
    消息提示文案
  <span class="hljs-tag">&lt;/<span class="hljs-name">bs-alert</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">bs-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"warning"</span>&gt;</span>
    警告提示文案
  <span class="hljs-tag">&lt;/<span class="hljs-name">bs-alert</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">bs-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"danger"</span>&gt;</span>
    错误提示文案
  <span class="hljs-tag">&lt;/<span class="hljs-name">bs-alert</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
</div>
<h3>关闭按钮</h3>
<div class="bs-example"><template>
  <bs-alert v-show="isShow" type="danger" :closable="true" @on-close="isShow = false">
    可以关闭的提示
  </bs-alert>
  <bs-alert type="info" :closable="false">
    不可关闭的提示
  </bs-alert>
</template>
</div>
                    <div class="highlight"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">bs-alert</span> <span class="hljs-attr">v-show</span>=<span class="hljs-string">"isShow"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"danger"</span> <span class="hljs-attr">:closable</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">@on-close</span>=<span class="hljs-string">"isShow = false"</span>&gt;</span>
    可以关闭的提示
  <span class="hljs-tag">&lt;/<span class="hljs-name">bs-alert</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">bs-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span> <span class="hljs-attr">:closable</span>=<span class="hljs-string">"false"</span>&gt;</span>
    不可关闭的提示
  <span class="hljs-tag">&lt;/<span class="hljs-name">bs-alert</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
</div>
<h3>关闭事件</h3>
<div class="bs-example"><template>
  <bs-alert :type="item.type" :closable="true" @on-close="close(index)" v-for="(item, index) in list">
    {{item.msg}}
  </bs-alert>
</template>

</div>
                    <div class="highlight"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">bs-alert</span> <span class="hljs-attr">:type</span>=<span class="hljs-string">"item.type"</span> <span class="hljs-attr">:closable</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">@on-close</span>=<span class="hljs-string">"close(index)"</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"(item, index) in list"</span>&gt;</span>
    <span>{{</span>item.msg<span>}}</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">bs-alert</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">list</span>: [
          {
            <span class="hljs-attr">type</span>: <span class="hljs-string">'danger'</span>,
            <span class="hljs-attr">msg</span>: <span class="hljs-string">'关闭事件'</span>
          },
          {
            <span class="hljs-attr">type</span>: <span class="hljs-string">'info'</span>,
            <span class="hljs-attr">msg</span>: <span class="hljs-string">'关闭事件'</span>
          },
          {
            <span class="hljs-attr">type</span>: <span class="hljs-string">'success'</span>,
            <span class="hljs-attr">msg</span>: <span class="hljs-string">'关闭事件'</span>
          },
          {
            <span class="hljs-attr">type</span>: <span class="hljs-string">'warning'</span>,
            <span class="hljs-attr">msg</span>: <span class="hljs-string">'关闭事件'</span>
          }
        ]
      }
    },
    <span class="hljs-attr">methods</span>: {
      close (index) {
        <span class="hljs-keyword">this</span>.list.splice(index, <span class="hljs-number">1</span>)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
<h3>Attributes</h3>
<table class="table table-bordered"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>主题</td>
<td>string</td>
<td>success, warning, info, dange</td>
<td>info</td>
</tr>
<tr>
<td>closable</td>
<td>是否显示关闭按钮</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3>Events</h3>
<table class="table table-bordered"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-close</td>
<td>点击关闭按钮时触发的事件</td>
<td>—</td>
</tr>
</tbody>
</table>

</section></template>

<script>
  export default {
    data () {
      return {
        list: [
          {
            type: 'danger',
            msg: '关闭事件'
          },
          {
            type: 'info',
            msg: '关闭事件'
          },
          {
            type: 'success',
            msg: '关闭事件'
          },
          {
            type: 'warning',
            msg: '关闭事件'
          }
        ],
        isShow: true
      }
    },
    methods: {
      close (index) {
        this.list.splice(index, 1)
      }
    }
  }
</script>


